<template>
  <div
    :style="'height:'+containerHeight+'px;overflow-y:auto;background:'+loginBackground+';background-repeat: no-repeat;background-size:cover; background-attachment:fixed;'">
    <div
      :style="'text-align:center;width: 300px; margin-left: auto;margin-right: auto;margin-top:' +containerHeight/3+'px'">
      <Card class="user_login_dialog" dis-hover>
        <Form ref="login" :model="loginForm" :label-width="0" class="login-form" :rules="validateRules">
          <FormItem label="" prop="email">
            <Input type="email" size="large" style="height: 48px;" v-model="loginForm.email" placeholder="登录邮箱"/>
          </FormItem>
          <FormItem label="" prop="Title">
            <Input type="password" size="large" style="height: 48px;" v-model="loginForm.password" placeholder="登录密码"/>
          </FormItem>
          <Button class="login-button2" shape="circle" long @click="login">登录</Button>
        </Form>

      </Card>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import { loginValidateRules } from './login'

export default {
  name: 'HomeUnlogin',
  props: [],
  data () {
    return {
      loginForm: {
        email: '',
        password: ''
      },
      validateRules: loginValidateRules
    }
  },
  computed: {
    ...mapGetters(['appBodyHeight', 'userInfo']),
    ...mapGetters('systemglobal', ['createDialogShow']),
    containerHeight: function () {
      return this.appBodyHeight
    },

    routerName: function () {
      return this.$route.name
    },
    loginBackground: function () {
      let random_num = Math.floor(Math.random() * (10 - 1)) + 1
      return 'url(/static/global/images/login/login' + random_num + '.jpg)'
    }
  },


  methods: {
    ...mapMutations(['setLogin', 'setUserInfo']),
    ...mapMutations('systemglobal', ['setCreateDialogShow', '']),

    updateProfiles: function () {

    },

    changeAvatar: function () {

    },


    login: function () {
      this.$axios.post('/api/common/user/login', this.loginForm).then(response => {
        if (!response.data.result.login) {
          this.$Message.error({
            content: response.data.result.message,
            duration: 10,
            closable: true
          })
        } else {
          this.$cookie.set('sessionid', response.data.result.sessionid)
          localStorage.setItem('login', true)
          this.$router.push({ path: '/home/summary' })
        }
      }, response => {
      })
    }
  },

  created: function () {
  },

  mounted: function () {

  },

  watch: {},

  components: {}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

  .slogan {
    font-family: '幼圆' !important;
    font-size: 35px !important;
    margin-top: 25px;
    margin-bottom: 25px;
    display: block;
    font-weight: 400;
    text-align: center;
    letter-spacing: 12px;
    color: #323A45;
    opacity: 0.6 !important;
  }

  .appName {
    font-family: 'Dotum' !important;
    font-size: 30px;
    opacity: .4;
    font-weight: 400;
    text-align: center;
    color: #323A45;
    width: 270px;
  }

  .user_login_dialog {
    width: 480px;
    height: 360px;
    border-radius: 10px;
    position: fixed;
    left: 50%;
    margin-left: -240px;
    top: 30%;
    background: rgba(220, 220, 220, 0.6);
  }

  .login-form {
    width: 100%;
    height: 100%;
    margin-top: -130px;
    padding: 20px 50px 50px 50px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    position: absolute;
    left: 50%;
    margin-left: -240px;
    top: 50%;
    color: #FFF;
  }

  .login-button2 {
    /*background-color: #2eb06f!important;*/
    /*border-radius: 10px;*/
    border-color: #202d40;
    width: 100%;
    font-size: 24px;
    height: 48px;
    padding-top: 0px;
    margin-top: 30px;
  }

  .ivu-input-large {
    font-size: 14px;
    padding: 6px 7px;
    height: 48px;
  }

</style>

<style lang="less">

  .login-form .ivu-input-large {
    font-size: 16px;
    padding: 6px 7px;
    height: 48px;
  }

</style>



